import React from 'react';
import styled from 'styled-components';

const Container = styled.div``;

const Input = styled.input`
  border: none;
  border-radius: 5px;
  font-size: 28px;
  padding: 10px 15px;
  background: hsla(0, 100%, 100%, 0.2);
  outline: none;
  color: #ffffff;
  letter-spacing: 1px;
  width: 100%;
`;

const TextInput = ({ name, value, handleChange, className }) => (
  <Container className={className}>
    <Input value={value} onChange={({ target: { value } }) => handleChange(name, value)} />
  </Container>
);

export default TextInput;



