import styled from 'styled-components';

const Paragraph = styled.p`
  margin: 0;
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  text-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);

  &:not(:last-child) {
    margin-bottom: 20px;
  }
`;

export default Paragraph;



