import _ from 'lodash';
import React from 'react';
import styled from 'styled-components';
import genAnimation from '../genAnimation';
import FormControls from './FormControls';
import Title from './Title';

const Container = styled.div``;

const AnimTitle = styled(Title)`
  font-size: 28px;
  font-weight: 300;
  ${genAnimation(500)};
`;

const QuestionAnimation = styled.div`
  ${genAnimation(750)};
`;

const AnimFormControls = styled(FormControls)`
  ${genAnimation(1250)};
`;

class FormQuestions extends React.Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.next = this.next.bind(this);

    this.state = {
      qIndex: 1,
      questions: props.questions,
      values: props.questions.reduce((obj, q) => ({ ...obj, [q.name]: q.default }), {}),
    };
  }

  handleChange(name, value) {
    this.setState(prev => ({
      values: { ...prev.values, [name]: value },
    }));
  }

  next() {
    this.setState(prev => ({
      qIndex: _.clamp(prev.qIndex + 1, 0, prev.questions.length - 1),
    }));
  }

  render() {
    let { qIndex, questions, values } = this.state;

    let { component: QComponent, ...question } = questions[qIndex];

    return (
      <Container>
        {/* {questions.map(q => ( */}
        <div>
          <AnimTitle>{question.title}</AnimTitle>
          <QuestionAnimation>
            <QComponent {...question} value={values[question.name]} handleChange={this.handleChange} />
          </QuestionAnimation>
        </div>
        {/* ))} */}
        <AnimFormControls handleNextClick={this.next} />
      </Container>
    );
  }
}

FormQuestions.defaultProps = {
  questions: [],
};

export default FormQuestions;



