import React from 'react';
import { Route, Switch, withRouter } from 'react-router-dom';
import styled from 'styled-components';
import { CALCULATORS_PAGE_ROUTE, FORM_LOADING_PAGE_ROUTE, FORM_PAGE_ROUTE, LOADING_PAGE_ROUTE } from '../constants';
import backgroundImage from '../images/clouds-bg.jpg';
import CalculatorsPage from '../pages/Calculators';
import FormPage from '../pages/Form';
import FormLoadingPage from '../pages/FormLoading';
import LoadingPage from '../pages/Loading';
import FadeAnimationWrapper from './FadeAnimationWrapper';

class Root extends React.Component {
  render() {
    return (
      <Route
        path="/"
        render={({ location }) => (
          <FadeAnimationWrapper>
            <Content>
              <Switch location={location}>
                <Route exact path={LOADING_PAGE_ROUTE} component={LoadingPage} />
                <Route path={CALCULATORS_PAGE_ROUTE} component={CalculatorsPage} />
                <Route path={FORM_LOADING_PAGE_ROUTE} component={FormLoadingPage} />
                {/* <Route path={FORM_PAGE_ROUTE} component={FormPage} /> */}
              </Switch>
            </Content>
          </FadeAnimationWrapper>
        )}
      />
    );
  }
}

const Content = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #efefef;
  background-image: url(${backgroundImage});
  background-position: center bottom;

  &.fade-enter-active,
  &.fade-exit-active {
    pointer-events: none;
  }
`;

export default withRouter(Root);



