import React from 'react';
import styled from 'styled-components';
import { Slider as RCSlider, Rail, Handles, Tracks, Ticks } from 'react-compound-slider';
import { RANGE_SLIDER_HANDLE_SIZE, RANGE_SLIDER_RAIL_SIZE } from '../constants';

const Container = styled.div`
  margin-top: 10px;
  width: 50%;
`;

const Slider = styled(RCSlider)`
  position: relative;
`;

const RailChild = styled.div`
  height: ${RANGE_SLIDER_RAIL_SIZE}px;
  border-radius: 5px;
  background-color: white;
  cursor: pointer;
`;

const Handle = styled.div`
  position: absolute;
  left: calc(${p => p.percent}% - ${RANGE_SLIDER_HANDLE_SIZE / 2}px);
  margin: -${(RANGE_SLIDER_HANDLE_SIZE + RANGE_SLIDER_RAIL_SIZE) / 2}px 0 15px;
  z-index: 2;
  width: ${RANGE_SLIDER_HANDLE_SIZE}px;
  height: ${RANGE_SLIDER_HANDLE_SIZE}px;
  cursor: pointer;
  border-radius: 15px;
  background-color: #eeeeee;

  &:after {
    content: '${p => p.value}';
    position: absolute;
    top: 50%;
    left: 50%;
    color: #999999;
    font-size: 14px;
    transform: translate(-50%, -50%);
  }
`;

const RangeSlider = ({ name, value, min, max, step, handleChange }) => (
  <Container>
    <Slider domain={[min, max]} values={[value]} step={step} onChange={([v]) => handleChange(name, v)}>
      <Rail>{({ getRailProps }) => <RailChild {...getRailProps()} />}</Rail>
      <Handles>{({ handles: [handle], getHandleProps }) => <Handle {...{ ...getHandleProps(handle.id), ...handle }} />}</Handles>
    </Slider>
  </Container>
);

export default RangeSlider;



