import React from 'react';
import styled from 'styled-components';
import { default as TitleComp } from './Title';
import LightContainer from './LightContainer';

const Animation = styled.a`
  display: block;
  text-decoration: none;
  cursor: pointer;
  user-select: none;

  &:not(:last-child) {
    margin-bottom: 40px;
  }

  ${p => p.animation};
`;

const Container = styled(LightContainer)`
  transition: all 150ms ease;

  &:hover {
    transform: scale(1.01);
    background: hsla(0, 100%, 100%, 0.3);
  }
`;

const Title = styled(TitleComp)`
  font-size: 28px;
`;

const CanvasButton = ({ title, animation, onClick }) => (
  <Animation animation={animation} onClick={onClick}>
    <Container>
      <Title>{title}</Title>
    </Container>
  </Animation>
);

CanvasButton.defaultProps = {
  text: '',
  animation: '',
  onClick: () => {},
};

export default CanvasButton;



