import React from 'react';
import styled from 'styled-components';
import data from '../canvases.js';
import DarkFillContainer from '../components/DarkFillContainer';
import FormQuestions from '../components/FormQuestions';
import Logo from '../components/Logo';
import Title from '../components/Title';
import genAnimation from '../genAnimation';
import { FormValuesProvider } from '../components/FormValuesContext.js';

const Content = styled.div`
  width: 100%;
  max-width: 960px;
  margin: 40px auto 0;
`;

const AnimTitle = styled(Title)`
  ${genAnimation()};
`;

const FormWrapper = styled.div`
  margin-top: 80px;
`;

export default class FormPage extends React.Component {
  constructor(props) {
    super(props);

    let { canvasId: cId } = props.match.params;

    const canvas = data.find(c => c.id === cId) || {};

    this.state = {
      canvas,
      questions: canvas.questions,
      values: canvas.questions.reduce((obj, q) => ({ ...obj, [q.name]: q.default }), {}),
    };
  }

  render() {
    let { canvas } = this.state;

    return (
      <FormValuesProvider value={this.state}>
        <DarkFillContainer>
          <Content>
            <Logo />
            <AnimTitle>{canvas.title}</AnimTitle>
            <FormWrapper>
              <FormQuestions questions={canvas.questions} />
            </FormWrapper>
          </Content>
        </DarkFillContainer>
      </FormValuesProvider>
    );
  }
}



