import React from 'react';
import styled from 'styled-components';
import spinnerImage from '../images/loading.svg';

const Container = styled.div`
  text-align: center;
`;

const LoadingText = styled.div`
  font-size: 36px;
  color: #ffffff;
  text-align: center;
  text-shadow: 0px 0px 40px rgba(0, 0, 0, 0.25);
`;

const Message = styled(LoadingText)`
  max-width: 20em;
  margin: 0 auto;
  font-family: 'Marck Script', serif;
`;

const Spinner = styled.img`
  margin-top: 20px;
  width: 50px;
  height: 20px;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
`;

const LoadingMessage = ({ children, message, ...rest }) => (
  <Container {...rest}>
    <LoadingText>{children}</LoadingText>
    <Spinner src={spinnerImage} />
    {message && <Message>{message}</Message>}
  </Container>
);

LoadingMessage.defaultProps = {
  children: 'Loading',
  message: null,
};

export default LoadingMessage;



