import _ from 'lodash';
import React from 'react';
import fadeIn from 'react-animations/lib/fade-in';
import { Redirect } from 'react-router-dom';
import styled from 'styled-components';
import DarkFillContainer from '../components/DarkFillContainer';
import LoadingMessage from '../components/LoadingMessage';
import Logo from '../components/Logo';
import { FORM_LOADING_TIME, FORM_PAGE_ROUTE } from '../constants';
import genAnimation from '../genAnimation';

const quotes = [
  'Deserunt sit duis amet laborum duis occaecat est non cupidatat proident eu ad.',
  'Duis velit excepteur pariatur quis tempor dolor nostrud.',
  'Aliqua ad ex amet irure.',
  'Qui id dolor sint in.',
  'Incididunt culpa ut minim ipsum tempor eu do cillum duis ex mollit culpa aliquip dolor.',
  'Labore fugiat duis adipisicing culpa exercitation magna ut duis.',
  'Nulla magna aliquip dolor eiusmod labore do.',
];

const Content = styled.div`
  width: 100%;
  max-width: 960px;
  margin: 40px auto 0;
`;

const AnimLoadingMessage = styled(LoadingMessage)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  ${genAnimation(0, true, fadeIn)};
`;

const FormWrapper = styled.div`
  margin-top: 80px;
`;

export default class FormLoadingPage extends React.Component {
  state = {
    redirect: false,
    quote: quotes[_.random(0, quotes.length - 1, false)],
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        redirect: true,
      });
    }, FORM_LOADING_TIME);
  }

  shouldComponentUpdate() {
    return !this.state.redirect;
  }

  render() {
    let { canvasId } = this.props.match.params;
    let { redirect, quote } = this.state;

    return (
      <DarkFillContainer>
        <Content>
          {redirect && <Redirect to={FORM_PAGE_ROUTE.replace(':canvasId', canvasId)} />}
          <Logo />
          <FormWrapper>
            <AnimLoadingMessage message={quote}>Loading Calculator</AnimLoadingMessage>
          </FormWrapper>
        </Content>
      </DarkFillContainer>
    );
  }
}



