import React from 'react';
import styled from 'styled-components';
import _ from 'lodash';
import { withRouter } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { PAGE_ANIMATION_TIME } from '../constants';

const Wrapper = styled.div`
  ${p => p.relative && 'position: relative'};

  .fade-enter {
    opacity: 0.01;
    z-index: 999;
  }

  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity ${PAGE_ANIMATION_TIME}ms ease-in;
  }

  .fade-exit {
    opacity: 1;
    z-index: 0;
  }

  .fade-exit.fade-exit-active {
    opacity: ${p => (p.crossFade ? 0.01 : 1)};
    transition: opacity ${PAGE_ANIMATION_TIME}ms ease-in;
  }
`;

const FadeAnimationWrapper = ({ children, childKey, location, ...rest }) => (
  <Wrapper {...rest}>
    <TransitionGroup>
      <CSSTransition
        key={!_.isUndefined(childKey) ? childKey : location.key}
        timeout={{ enter: PAGE_ANIMATION_TIME, exit: PAGE_ANIMATION_TIME }}
        classNames={'fade'}
      >
        {children}
      </CSSTransition>
    </TransitionGroup>
  </Wrapper>
);

export default withRouter(FadeAnimationWrapper);



