import React from 'react';
import styled from 'styled-components';
import _ from 'lodash';
import CanvasButton from './CanvasButton';
import CanvasDetails from './CanvasDetails';
import genAnimation from '../genAnimation';
import FadeAnimationWrapper from './FadeAnimationWrapper';

import data from '../canvases.js';

const Container = styled.div`
  position: absolute;
  width: 100%;
`;

export default class Canvases extends React.Component {
  state = {
    canvasId: null,
    animate: true,
  };

  chooseCanvas(canvasId) {
    this.setState({
      animate: false,
    });

    this.setState({
      canvasId,
    });
  }

  render() {
    let { canvasId, animate } = this.state;
    let { delayAnimation } = this.props;

    let canvas = _.find(data, c => c.id === canvasId);

    return (
      <FadeAnimationWrapper childKey={canvasId} relative crossFade>
        {!canvas ? (
          <Container>
            {data.map((canvas, index) => (
              <CanvasButton
                key={canvas.id}
                {...canvas}
                animation={animate && genAnimation(delayAnimation + 250 * index)}
                onClick={() => this.chooseCanvas(canvas.id)}
              />
            ))}
          </Container>
        ) : (
          <Container>
            <CanvasDetails {...canvas} back={() => this.chooseCanvas(null)} />
          </Container>
        )}
      </FadeAnimationWrapper>
    );
  }

  static defaultProps = {
    delayAnimation: 0,
  };
}



