import React from 'react';
import styled from 'styled-components';
import PrimaryButton from './PrimaryButton';
import TextButton from './TextButton';

const Container = styled.div`
  margin-top: 20px;
`;

const SkipButton = styled(TextButton)`
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 1px;
  margin-left: 15px;
`;

const FormControls = ({ handleNextClick, handleSkipClick, className }) => (
  <Container className={className}>
    {handleNextClick && <PrimaryButton onClick={handleNextClick}>Next</PrimaryButton>}
    {handleSkipClick && <SkipButton onClick={handleSkipClick}>Skip</SkipButton>}
  </Container>
);

export default FormControls;



