import React from 'react';
import styled from 'styled-components';
import DarkFillContainer from '../components/DarkFillContainer';
import Title from '../components/Title';
import Logo from '../components/Logo';
import Canvases from '../components/Canvases';
import genAnimation from '../genAnimation';

const Content = styled.div`
  width: 100%;
  max-width: 960px;
  margin: 40px auto 0;
`;

const AnimLogo = styled(Logo)`
  ${genAnimation()};
`;

const AnimTitle = styled(Title)`
  ${genAnimation(250)};
`;

const CanvasesWrapper = styled.div`
  margin-top: 80px;
`;

export default class CalculatorsPage extends React.Component {
  render() {
    return (
      <DarkFillContainer>
        <Content>
          <AnimLogo />
          <AnimTitle>Choose Your Canvas</AnimTitle>
          <CanvasesWrapper>
            <Canvases delayAnimation={750} />
          </CanvasesWrapper>
        </Content>
      </DarkFillContainer>
    );
  }
}



