import styled from 'styled-components';

const Title = styled.h1`
  margin: 0;
  color: #ffffff;
  font-size: 36px;
  font-weight: 400;
  text-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);

  &:not(:last-child) {
    margin-bottom: 10px;
  }
`;

export default Title;



