import { PAGE_ANIMATION_TIME } from './constants';
import { keyframes } from 'styled-components';
import fadeInUp from 'react-animations/lib/fade-in-up';

export default function(delayOffset = 0, afterPageTransition = true, animationType = fadeInUp) {
  let animation = keyframes(animationType);
  let delay = (afterPageTransition ? PAGE_ANIMATION_TIME : 0) + delayOffset;

  return `
    opacity: 0;
    animation: 1s ${delay}ms ${animation} forwards;
  `;
}



