import styled from 'styled-components';

const TextButton = styled.div`
  display: inline-block;
  padding: 5px 0;
  color: ${p => p.color};
  border-radius: 5px;
  opacity: 1;
  cursor: pointer;
  transition: all 250ms ease;

  &:hover {
    opacity: 0.6;
  }
`;

TextButton.defaultProps = {
  color: '#ffffff',
};

export default TextButton;



