import React from 'react';
import styled from 'styled-components';
import { Link as RRLink } from 'react-router-dom';
import { default as TitleComp } from './Title';
import Paragraph from './Paragraph';
import LightContainer from './LightContainer';
import PrimaryButton from './PrimaryButton';
import TextButton from './TextButton';
import { FORM_LOADING_PAGE_ROUTE } from '../constants';

const Title = styled(TitleComp)`
  font-size: 28px;
`;

const Link = styled(RRLink)`
  text-decoration: none;
`;

const CanvasDetails = ({ id, title, description, back }) => (
  <LightContainer>
    <TextButton onClick={back}>Back to Canvases</TextButton>
    <Title>{title}</Title>
    <Paragraph>{description}</Paragraph>
    <Link to={FORM_LOADING_PAGE_ROUTE.replace(':canvasId', id)}>
      <PrimaryButton outline>Continue</PrimaryButton>
    </Link>
  </LightContainer>
);

CanvasDetails.defaultProps = {
  id: '',
  title: '',
  description: '',
  onClick: () => {},
};

export default CanvasDetails;



