import styled from 'styled-components';

const PrimaryButton = styled.div`
  display: inline-block;
  padding: 5px 10px;
  ${p => !p.outline && 'background-color: ' + p.color};
  color: ${p => (p.outline ? p.color : '#999999')};
  border: 1px solid ${p => (p.outline ? p.color : 'transparent')};
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: all 250ms ease;

  &:hover {
    background-color: ${p => (p.outline ? p.color : '#000000')};
    color: #999999;
    ${p => !p.outline && 'opacity: 0.8'};
  }
`;

PrimaryButton.defaultProps = {
  color: '#ffffff',
  outline: false,
};

export default PrimaryButton;



